
<el-upload
	ref="upload"
	action=""
	:accept="this.accept"
	:auto-upload="false"
	:on-change="changeFile"
	:http-request="uploadHandler">
	<el-button plain slot="trigger" size="small">
		<i class="el-icon-upload font-size-14"></i>读取文件
	</el-button>
	<div class="text-info">仅支持{{this.accept}}后缀文件上传，最大2MB</div>
</el-upload>
